.btn {
  background-color: rgba(255, 255, 255, 0.395);
  backdrop-filter: blur(3px);
  border: 0;
  margin: 1rem 1rem;
  height: 5rem;
  width: 12rem;
  cursor: pointer;
  transition: ease-in 0.3s;
  border-radius: 5px;
}

.btn h3 {
  color: white;
  font-size: large;
}

.btn__heading {
  color: white;
}

.btn:hover {

  background-color: #1976d2;
  box-shadow: 0 0 10px 3px rgba(255, 255, 255, 0.432);
}


@media all and (max-width: 900px) {
  .btn{
    height: 3rem;
    /* width: 9rem; */
    margin: 0.5rem 0.5rem;
  }

  .btn h3{
  font-size: medium;

  }
}
