.event__div {
  min-height: 90vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 25px;
}

.grid {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
  width: 80%;
}

.grid_item {
  width: 40%;
}

@media all and (max-width: 900px) {
  .grid {
    flex-direction: column;
    margin-top: 60px;
  }
  .grid_item {
    text-align: center;
    width: 95%;
  }
}
