.nav {
  margin: 0;
  width: 100%;
  height: 7rem;
  background-color: #121210;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  width: 80%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.logo {
  flex-shrink: 0;
  min-width: 5rem;
  max-width: 10rem;
}

.list {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  padding: 0;
}

.list li {
  text-decoration: none;
  color: white;
  list-style: none;
  margin: 0 1rem;
}

.nav_links__container {
  width: 100%;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.menu__icon div {
  background-color: white;
  width: 30px;
  height: 3px;
  margin: 6px 0;
}

.menu__icon {
  cursor: pointer;
  display: none;
}

@media all and (max-width: 900px) {
  .menu__icon {
    display: inline;
  }
  .list{
    display: none;
  }
}
@media all and (max-width: 1200px) {
  .list li {
    margin: 0 0.5rem;
  }
}
