.timeline__container
{
  min-height: 110vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: black;
  display: none;
  transition: all 1s ease-in;
}

.timeline__button :active
{
  display: block;
}

.timeline__tree__container
{
  min-width: 100%;
}

.timeline__buttons__div {
  width: 100%;
  min-height: 5rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  padding: 1rem 0;
}
.timeline__button {
  background: transparent;
  border: 2px solid white;
  border-radius: 5px;
  color: white;
  margin:0.5rem;
  width: 14rem;
  height: 4rem;
  transition: all 0.2s ease-in;
  font-size: large;
  cursor: pointer;
}

.timeline__button:hover {
  background-color: white;
  color: black;
  font-size: larger;
}

.active__button {
  background-color: white;
  color: black;
  font-size: larger;
}
