.social__links {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.lastline
{
    width: 100%;
    display: flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
}

.hr{
    width: 40%;
    text-decoration: none;
    background-color: #969696;
    height: 0.1rem;
    border: none;
    margin-bottom: 5rem;
}

.newsletter__input
{
    background-color: transparent;
    color: white;
    border: 2px solid white;
    width: 100%;
    height: 3rem;
    font-weight: 700;
    font-size: 1rem;
    padding-left: 10px;
}

input::placeholder {
    font-weight: bold;
    font-size: medium;
    opacity: 0.5;
    color: white;
}

.newsletter__button
{
    color: white;
    border: 2px solid #969696;
    padding: 10px 30px;
    background-color: transparent;
    font-size: 1.4rem;
    cursor: pointer;
    transition: all 0.3s;
}

.newsletter__button:hover{
    border-color: white;
    background-color: white;
    color: black;
}