
.card {
  width: 20rem;
  height: 17rem;
  min-height: 10rem;
  min-width: 10rem;
  background-color: transparent;
  box-shadow: 5px 5px gray;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid gray;
  border-radius: 20px;
  transition: all ease-in 0.2s;
}

.card img {
  width: 80%;
  transition: all ease-in 0.2s;
}

.content {
  bottom: -20px;
  left: 0;
  z-index: -1;
  display: block;
  width: 300px;
  margin: 0;
  margin-top: 10px;
  padding: 0;
  color: #444;
  font-size: 18px;
  text-decoration: none;
  text-align: center;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  opacity: 0;
}
.card:hover + .content {
  bottom: -36px;
  opacity: 1;
}

.card:hover {
  box-shadow: -5px -5px gray;
  background-color: rgba(255, 255, 255, 0.212);
}
