.ticket__info {
  width: 100%;
  background-color: white;
  transition: all 0.2s;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.112);
  border-radius: 10px;
}


.ticket__info__container {
  padding: 1rem;
}

.img
{
  width: 100%;
  height: 500px;
  border-radius: 20px;
  overflow: hidden;
}

.img img{
  height: 100%;
  width: 100%;
}

@media all and (max-width: 900px) {
  .img{
    display: none;
  }
}

@media all and (max-width: 900px) {
  .ticket__info { 
    position: fixed; 
    left: 0 ; right: 0; bottom: 0; 
    height:[footer-height] 
  }
}