.remainingTime__div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 10rem;
  background-color: #121210;
}

.remainingTime__content {
  color: white;
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.remainingTime__content h4{
  color: white;
  font-size: 1rem;
}

@media all and (max-width: 900px) {
  .remainingTime__content{
    width: 95%  ;
  }
  .remainingTime__content span{
    display: none;
  }
}
