.card {
    width: 20rem;
    height: 17rem;
    min-height: 10rem;
    min-width: 10rem;
    background-color: transparent;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;

  }
  
  .card img {
      width: 100%;
      height: 100%;
  }