.container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 4rem auto;
  width: 20rem;
  height: 15rem;
  background: transparent;
  border-radius: 25px;
  font-size: 20px;
  letter-spacing: 2px;
  transition: 1s box-shadow;
}
.featured__img {
  width: 100%;
  position: relative;
}
.featured__img img {
  width: 100%;
  height: 100%;
  display: block;
}
.featured__img:hover:before {
  bottom: 1rem;
  right: 1rem;
}
.featured__img:hover:after {
  top: 1rem;
  left: 1rem;
}
.featured__img:before,
.featured__img:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  transition: 0.3s ease;
}
.featured__img:before {
  background: black;
  bottom: 0.4rem;
  right: 0.4rem;
}
.featured__img:after {
  background: black;
  top: 0.4rem;
  left: 0.4rem;
}

.cover {
 
  background-color: black;
  transition: all 0.2s;
  color: white;
  width: 100%;
  height: 100%;
  transition: 0.5s ease;
  opacity: 0;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
}

.cover:hover{
  opacity: 0.85;
}