.event__div {
  min-height: 85vh;
  width: 100%;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  
}

.grid {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
  width: 80%;
}

.grid_item {
  width: 40%;
}

@media all and (max-width: 900px) {
  .grid {
    flex-direction: column;
  }
  .grid_item {
    text-align: center;
    margin: 1rem;
    width: 95%;
  }
}
